import React from "react";

function Duration({ hours, minutes }) {
  const formatHours = () => {
    if (hours > 9 && hours < 20) return "—á–∞—Å–æ–≤";
    const hoursLastDigit = +hours.substring(hours.length - 1);
    switch (hoursLastDigit) {
      case 1:
        return "—á–∞—Å";
      case 2:
      case 3:
      case 4:
        return "—á–∞—Å–∞";
      default:
        return "—á–∞—Å–æ–≤";
    }
  };

  const formatMinutes = () => {
    if (minutes > 9 && minutes < 20) return "–º–∏–Ω—É—Ç";
    const minutesLastDigit = +minutes.substring(minutes.length - 1);
    switch (minutesLastDigit) {
      case 1:
        return "–º–∏–Ω—É—Ç–∞";
      case 2:
      case 3:
      case 4:
        return "–º–∏–Ω—É—Ç—ã";
      default:
        return "–º–∏–Ω—É—Ç";
    }
  };

  return (
    <div className="train__info-duration">
      {hours} {formatHours()}
      {"\n"} {minutes} {formatMinutes()}
    </div>
  );
}

export default Duration;
